import React from "react";

const Async = () => {
  return (
    <div className="headertext">
      <div className="async">
        <h1> </h1>
        <p>
                 window.fetch 
           AJAX-,  Axios  jQuery AJAX.  
               componentDidMount.
        </p>
        <h3>:    AJAX-</h3>
        <p>
            ,   componentDidMount  
             AJAX-.
        </p>
        <div className="componentscode">
          <pre>
            <code>
              {`class MyComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: []
    };
  }

  componentDidMount() {
    fetch("https://api.example.com/items")
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            items: result.items
          });
        },
        // :     ,     catch(),
        //         .
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }

  render() {
    const { error, isLoaded, items } = this.state;
    if (error) {
      return <div>: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>...</div>;
    } else {
      return (
        <ul>
          {items.map(item => (
            <li key={item.id}>
              {item.name} {item.price}
            </li>
          ))}
        </ul>
      );
    }
  }
}
                `}
            </code>
          </pre>
        </div>
        <p>   :</p>
        <div className="componentscode">
          <pre>
            <code>
              {`function MyComponent() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);

  // :    [] , 
  //  useEffect    
  //  componentDidMount()
  useEffect(() => {
    fetch("https://api.example.com/items")
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result);
        },
        // :     ,     catch(),
        //         .
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])

  if (error) {
    return <div>: {error.message}</div>;
  } else if (!isLoaded) {
    return <div>...</div>;
  } else {
    return (
      <ul>
        {items.map(item => (
          <li key={item.id}>
            {item.name} {item.price}
          </li>
        ))}
      </ul>
    );
  }
}
                `}
            </code>
          </pre>
        </div>
      </div>
    </div>
  );
};
export { Async };
