import React from "react";

function State() {
  return (
    <div className="headertext">
      <div className="state">
        <h1>C</h1>
        <div className="componentscode">
          <pre>
            <code>
              {`class Clock extends React.Component {
  constructor(props) {
    super(props);
    this.state = {date: new Date()};
  }

  render() {
    return (
      <div>
        <h1>, !</h1>
        <h2> {this.state.date.toLocaleTimeString()}.</h2>
      </div>
    );
  }
}
            `}
            </code>
          </pre>
        </div>
        <p>
          {" "}
           ,    props  ()
          :
        </p>
        <div className="componentscode">
          <pre>
            <code>
              {`constructor(props) {
  super(props);
  this.state = {date: new Date()};
}
          `}
            </code>
          </pre>
        </div>
        <p>
                 
           props.
        </p>
      </div>
    </div>
  );
}
export { State };
